@import url(https://fonts.googleapis.com/css?family=Annie+Use+Your+Telescope);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
  display: flex;
  flex-direction: column; }

#root {
  background-color: #1a1a1a; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.container {
  background-color: #eeeae7; }

.container--main {
  max-width: 700px;
  height: 100vh;
  flex-direction: column;
  display: flex;
  padding: 0; }

.container--login {
  max-width: 700px;
  height: 100vh;
  background-image: url(/happytenants/static/media/background.765ab131.jpg);
  background-position: 50%; }

.container--login_h1 {
  font-family: 'Annie Use Your Telescope', cursive;
  color: #f8f8f8;
  font-size: 40px; }

.row--list {
  flex: 1 1;
  flex-direction: column;
  display: flex;
  overflow: auto;
  margin: 0;
  background-image: url(/happytenants/static/media/doodles.d0aa956a.png); }

.row--chores {
  flex-grow: 1;
  padding: 0;
  margin: 0;
  background-image: url(/happytenants/static/media/doodles.d0aa956a.png); }

.row--zero {
  width: 100%;
  margin: 0;
  padding: 0; }

.row--nav {
  width: 100%;
  margin: 0;
  padding: 0;
  background-color: blueviolet; }

.row--login {
  height: 100vh; }

.none {
  margin: 0;
  padding: 0; }

.col--logo {
  flex-grow: 0;
  margin-bottom: 5px; }

.col--chores {
  flex-basis: 100%;
  padding: 0;
  margin: 0; }

.col--todoForm {
  flex-basis: 100%;
  padding: 0;
  margin: 0; }

.col--login {
  flex-basis: 100%;
  min-height: 127px; }

.logo {
  display: block;
  margin: 0 auto;
  max-width: 300px;
  max-height: 300px;
  padding-right: 40px; }

.logo--sm {
  display: inline-flex;
  margin: 0;
  width: 70px;
  height: 70px; }

.formTodo {
  width: 100%;
  display: flex; }

.width100 {
  width: 100%; }

.noRadius {
  border-radius: 0; }

.radius {
  border-radius: 25px;
  margin-left: 10px;
  margin-bottom: 10px; }

.header {
  font-family: 'Annie Use Your Telescope', cursive;
  display: inline-flex;
  align-self: center;
  background-color: blueviolet;
  color: white;
  font-size: 2.2rem;
  margin-bottom: 0px; }

.violet {
  background-color: #8a2be2;
  color: white;
  border-color: blueviolet; }

.violet:hover, .violet:active {
  background-color: #681baf !important;
  border-color: #681baf !important;
  box-shadow: 0 0 0 0.2rem rgba(174, 0, 255, 0.25) !important; }

.violet:focus {
  background-color: #681baf;
  box-shadow: 0 0 0 0.2rem rgba(174, 0, 255, 0.25); }

.form-control:focus {
  border-color: blueviolet;
  box-shadow: 0 0 0 0.2rem rgba(174, 0, 255, 0.25); }

.fontAwesomeIcon {
  display: inline-flex;
  color: blueviolet; }

.fontAwesomeIcon--white {
  color: white; }

.p--nav {
  color: blueviolet; }

.loginMsg {
  font-size: 10px;
  text-align: center; }

.optionText {
  word-wrap: break-word;
  font-weight: 400; }

a:hover {
  text-decoration: none; }

.endRow {
  background-color: #f8f8f8;
  width: 100%;
  margin: 0;
  border-top: 1px solid rgba(0, 0, 0, 0.308); }

.vl {
  border-left: 2px solid rgba(0, 0, 0, 0.123);
  position: relative;
  height: 60px;
  left: 50%;
  margin-left: -3px;
  top: 0; }

